}

.slider-wrapper {
  position: relative;
  height: 100%;
  width: 100%;
}

.slide {
  display: inline-block;
  height: 100%;
  width: 100%;
}

.arrow {
  height: 50px;
  width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f9f9f9;
  border-radius: 50%;
  cursor: pointer;
  -webkit-transition: -webkit-transform ease-in 0.1s;
  transition: -webkit-transform ease-in 0.1s;
  transition: transform ease-in 0.1s;
  transition: transform ease-in 0.1s, -webkit-transform ease-in 0.1s;
}

.nextArrow {
  position: absolute;
  top: 50%;
  right: 25px;
  z-index: 999;
  color: #fff;
}

.backArrow {
  position: absolute;
  top: 50%;
  left: 25px;
  z-index: 999;
  color: #fff;
}

.fa-arrow-right:before,
.fa-arrow-left:before {
  color: #222;
}

* {
  font-family: Raleway, sans-serif;
}

.flipperContainer {
  border-radius: 35px;
}
.flipperContainer,
.cardFront,
.cardBack {
  -webkit-perspective: 1000px;
  perspective: 1000px;
  width: 300px;
  height: 350px;
  position: relative;
  margin: 0 auto;
}
.flipperContainer:hover .flipper,
.flipperContainer.hover .flipper {
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
}
.flipper {
  -webkit-transition: 1s;
  transition: 1s;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  border-radius: 35px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
/* hide back of pane during swap */
.cardFront,
.cardBack {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

/* front pane, placed above back */
.cardFront {
  z-index: 99;
  /* for firefox 31 */
  -webkit-transform: rotateY(0deg);
  transform: rotateY(0deg);
}

/* back, initially hidden pane */
.cardBack {
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
}

.flex {
  display: flex;
  margin: 10px;
}

.header {
    position:fixed;
    width:100%;
    left:0;
    top:0;
    right: 0;
    z-index: 1000;
}

