  margin-bottom: 8px;
}
.product .product-price {
  font-size: 22px;
  font-weight: 700;
  line-height: 22px;
  margin-bottom: 16px;
}
.product .product-price:before {
  content: "\20B9   ";
}
.product .product-name,
.product .product-price {
  color: #666;
  padding: 0 16px;
  text-align: center;
}
.product .product-action {
  padding: 16px;
}
.product .product-action button {
  width: 100%;
  -webkit-transition: all 300ms ease-in;
  transition: all 300ms ease-in;
}
.product .product-action button.added {
  background: #fc7710;
}
.product:hover {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.stepper-input {
  display: flex;
  display: -webkit-flex;
  color: #666;
  max-width: 120px;
  margin: 0 auto;
}
.stepper-input .increment,
.stepper-input .decrement {
  height: 24px;
  width: 24px;
  border: 1px solid #ccc;
  text-align: center;
  box-sizing: border-box;
  border-radius: 50%;
  text-decoration: none;
  color: inherit;
  font-size: 24px;
  line-height: 22px;
  -moz-user-select: none;
  -webkit-user-select: none;
}
.stepper-input .increment:hover,
.stepper-input .decrement:hover {
  color: #077915;
  border-color: #077915;
}
.stepper-input .increment:active,
.stepper-input .decrement:active {
  color: #fff;
  border-color: #077915;
  background: #0bc122;
}
.stepper-input .quantity {
  height: 24px;
  width: 48px;
  text-align: center;
  margin: 0 12px;
  border-radius: 2px;
  border: 1px solid #ccc;
}
.stepper-input .quantity:focus {
  outline: none;
  border-color: #077915;
}

footer {
  color: #999;
  font-size: 14px;
  text-align: center;
  padding: 32px;
  background: #dddddd;
}
footer strong {
  color: #666;
}
footer .footer-links {
  margin-bottom: 24px;
}
footer .footer-links a {
  margin: 0 8px;
  color: #666;
}

.modal-wrapper {
  display: none;
  background: rgba(0, 0, 0, 0.66);
  position: fixed;
  top: 0;
  z-index: 100;
  height: 100%;
  width: 100%;
  overflow-y: hidden;
}
.modal-wrapper.active {
  display: flex;
  display: -webkit-flex;
  align-items: center;
  justify-content: center;
  -webkit-align-items: center;
  -webkit-justify-content: center;
}
.modal-wrapper .modal {
  width: 100%;
  max-width: 532px;
  margin: 16px;
  background: #fff;
  position: relative;
  border-radius: 4px;
}
.modal-wrapper .close {
  position: absolute;
  top: 0px;
  right: 0px;
  font-size: 24px;
  line-height: 36px;
  border-radius: 0 4px 0 0;
  background: transparent;
  color: #666;
}
.modal-wrapper .close:hover {
  background: #dddddd;
}

.quick-view-details {
  padding: 24px;
  background: #f1f1f1;
  border-radius: 0 0 4px 4px;
}
.quick-view-details .product-price {
  font-size: 18px;
}
.quick-view-details .product-price {
  color: #077915;
  font-weight: bold;
  font-size: 24px;
  float: right;
}
.quick-view-details .product-price:before {
  content: "\20B9   ";
}

.quick-view-image img {
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  display: block;
  border-radius: 4px 4px 0 0;
}

img {
  max-width: 100%;
  max-height: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  line-height: 160px;
}

.ant-carousel .slick-slide {
  text-align: center;
  height: 160px;
  line-height: 460px;
  background: #364d79;
  overflow: hidden;
}

.ant-carousel .slick-slide h3 {
  color: #fff;
}

